/**    HEADER **/
.navbar{
  background-color:#007bff;
}
.navbar .navbar-collapse >div.mr-auto >div >a {
    color: white;
 }
.navbar .navbar-collapse > div.mr-auto > div > a:hover {
     background-color: #cce5ff;
     color: #007bff;
     padding: 10px 15px;
     border-radius: 4px;
 }

.navbar .navbar-collapse > div.mr-auto > div > .active {
   background-color: #cce5ff;
   color: #007bff;
   font-weight:bold;
   padding: 10px 15px;
   border-radius: 4px;
}


/**    HOME **/
.row{
  background-color:white; 
  margin: 1%;
}
