.content-md{
  padding: .10%;
  text-align: center;
}
.home-header{
  text-align: center;
  padding: 2%;
}

.home-content{
  text-align: justify;
}
.card-img{
  height: 100px;
  width: 100px;
  display: block;
  padding:2%;
}
p.card-text{
  text-align: justify;
  word-spacing:-1px;
}
div.card-title{
  text-align: center;
}
.card-body {
  padding: .65rem;
}
span.filler{
  color:white;
}
.remove-border{
  border-radius: 0rem;
}


/***** Insurance ******/
.image-patient{
width:220px;
height:180px;
}


.card-img-insurance{
    display: block;
    padding:15%;
    width: 360px;
    height: 170px;
}



/*****Footer ****/
.footerRow
{
    background-color: transparent;
}

.footer-menu2 > .list-group-item,.footer-menu > .list-group-item  {
  background-color: transparent;
  color:white;
}
.list-group-item {
  background-color: transparent;
}
.footer-menu > .list-group-item > a > i{
  color: white;
}




.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.label-invalid-feedback-error,
.invalid-feedback-error{
    color: #dc3545;
}
div.invalid-feedback-error {
    width: 100%;
    margin-top: .25rem;
    font-size: 80%;
    color: #dc3545;
}
 .has-error-invalid-feedback-error 
{
    border-color: #dc3545;
    padding-right: 2.25rem;
    background-repeat: no-repeat;
    background-position: center right calc(2.25rem / 4);
    background-size: calc(2.25rem / 2) calc(2.25rem / 2);
    background-image: url(https://img.icons8.com/color/48/000000/delete-sign.png)
} 
